/**
 * ### Search Spaces
 *
 *   Returns an **array of space objects** that match the given search criteria.
 *
 *   If multiple search params are given and `filter_or` is FALSE or not specified,
 * search params are combined in a logical AND operation.
 * Only rows that match *all* search param criteria will be returned.
 *
 * If `filter_or` is TRUE, multiple search params are combined in a logical OR operation.
 * Results will include rows that match **any** of the search criteria.
 *
 * String search params use case-insensitive matching.
 * String search params can contain `%` and '_' as SQL LIKE pattern match wildcard expressions.
 * example="dan%" will match "danger" and "Danzig" but not "David"
 * example="D_m%" will match "Damage" and "dump"
 *
 * Integer search params can accept a single value or a comma separated list of values. The multiple
 * values will be combined under a logical OR operation - results will match at least one of
 * the given values.
 *
 * Most search params can accept "IS NULL" and "NOT NULL" as special expressions to match
 * or exclude (respectively) rows where the column is null.
 *
 * Boolean search params accept only "true" and "false" as values.
 *
 *
 *   The parameters `limit`, and `offset` are recommended for fetching results in page-size chunks.
 *
 *   Get a **single space** by id with [Space](#!/Space/space)
 *
 * GET /spaces/search -> ISpace[]
 *
 * @deprecated
 *
 * @param sdk IAPIMethods implementation
 * @param request composed interface "IRequestSearchSpaces" for complex method parameters
 * @param options one-time API call overrides
 *
 */
export const search_spaces = async (
  sdk: IAPIMethods,
  request: IRequestSearchSpaces,
  options?: Partial<ITransportSettings>
): Promise<SDKResponse<ISpace[], IError>> => {
  return sdk.get<ISpace[], IError>(
    '/spaces/search',
    {
      fields: request.fields,
      page: request.page,
      per_page: request.per_page,
      limit: request.limit,
      offset: request.offset,
      sorts: request.sorts,
      name: request.name,
      id: request.id,
      parent_id: request.parent_id,
      creator_id: request.creator_id,
      filter_or: request.filter_or,
      is_shared_root: request.is_shared_root,
    },
    null,
    options
  )
}

